/**
 * Enhanced CGPA Calculation
 * Standard formula: Σ(GP * Credits) / Σ Credits
 * Excludes: null grades, failed/incomplete grades (U, I, I_OP, etc.)
 */
export function calculateCGPA(courses: Course[]) {
    // Only include courses with a valid, non-failing/incomplete grade
    const validGrades: Exclude<Grade, null>[] = ['S', 'A', 'B', 'C', 'D', 'E'];
    const contributingCourses = courses.filter(c =>
        c.grade !== null && validGrades.includes(c.grade as any)
    );

    const totalGradePoints = contributingCourses.reduce((sum, c) => {
        const points = GRADE_POINTS[c.grade as Exclude<Grade, null>] || 0;
        return sum + (points * c.credits);
    }, 0);

    const totalCredits = contributingCourses.reduce((sum, c) => sum + c.credits, 0);

    const cgpa = totalCredits > 0 ? totalGradePoints / totalCredits : 0;

    return {
        cgpa: Number(cgpa.toFixed(2)),
        totalCredits,
        totalGradePoints,
        courseCount: contributingCourses.length
    };
}

/**
 * Mapping formula for T -> Grade
 * Used for detailed requirement 5 logic
 */
export function mapScoreToGrade(T: number): Exclude<Grade, null> {
    if (T >= 90) return 'S';
    if (T >= 80) return 'A';
    if (T >= 70) return 'B';
    if (T >= 60) return 'C';
    if (T >= 50) return 'D';
    if (T >= 40) return 'E';
    return 'U';
}
