
'use client';

import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Calculator as CalcIcon, ScrollText, Award, GraduationCap, RotateCcw, Info, CheckCircle2, Circle } from 'lucide-react';
import {
    Course,
    DegreeDomain,
    Grade,
    Level,
    GRADE_POINTS,
    calculateCGPA,
    CURRICULUM
} from '@/lib/grading';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';

type UserGrades = Record<DegreeDomain, Record<string, Grade>>;

export default function Calculator() {
    const [domain, setDomain] = useState<DegreeDomain>('Data Science');
    const [level, setLevel] = useState<Level>('Foundation');
    const [userGrades, setUserGrades] = useState<UserGrades>({
        'Data Science': {},
        'Electronic Systems': {}
    });
    const [isInitialized, setIsInitialized] = useState(false);

    // Load from localStorage on mount
    useEffect(() => {
        const saved = localStorage.getItem('iitm-cgpa-v2');
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                if (parsed.userGrades) setUserGrades(parsed.userGrades);
                if (parsed.domain) setDomain(parsed.domain);
                if (parsed.level) setLevel(parsed.level);
            } catch (e) {
                console.error('Failed to load storage', e);
            }
        }
        setIsInitialized(true);
    }, []);

    // Save to localStorage
    useEffect(() => {
        if (isInitialized) {
            localStorage.setItem('iitm-cgpa-v2', JSON.stringify({ userGrades, domain, level }));
        }
    }, [userGrades, domain, level, isInitialized]);

    // Compute display courses for current Level
    const currentCourses: Course[] = useMemo(() => {
        const officialList = CURRICULUM[domain][level];
        return officialList.map(c => ({
            id: `${domain}-${level}-${c.name}`,
            name: c.name,
            credits: c.credits,
            level: level,
            domain: domain,
            grade: userGrades[domain][c.name] ?? null
        }));
    }, [domain, level, userGrades]);

    // Compute Global Stats (across all levels for current domain)
    const stats = useMemo(() => {
        // We calculate based on ALL grades entered for the current domain
        const allLevels: Level[] = ['Foundation', 'Diploma', 'BSc Degree', 'BS Degree'];
        const allPossibleCourses: Course[] = allLevels.flatMap(l =>
            CURRICULUM[domain][l].map(c => ({
                id: `${domain}-${l}-${c.name}`,
                name: c.name,
                credits: c.credits,
                level: l,
                domain: domain,
                grade: userGrades[domain][c.name] ?? null
            }))
        );
        return calculateCGPA(allPossibleCourses);
    }, [domain, userGrades]);

    const updateGrade = (courseName: string, grade: Grade) => {
        setUserGrades(prev => ({
            ...prev,
            [domain]: {
                ...prev[domain],
                [courseName]: grade
            }
        }));
    };

    const resetLevelGrades = () => {
        if (confirm(`Clear all grades for ${level}?`)) {
            const updatedDomainGrades = { ...userGrades[domain] };
            CURRICULUM[domain][level].forEach(c => {
                delete updatedDomainGrades[c.name];
            });
            setUserGrades(prev => ({
                ...prev,
                [domain]: updatedDomainGrades
            }));
            toast.success(`Cleared ${level} grades`);
        }
    };

    if (!isInitialized) return null;

    return (
        <div className="space-y-8 max-w-6xl mx-auto p-4 md:p-8 animate-in fade-in duration-500">
            {/* Header & Configuration */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 items-start">
                <div className="lg:col-span-2 space-y-4">
                    <div className="flex items-center gap-3">
                        <div className="p-3 bg-green-600 text-white rounded-2xl shadow-lg ring-4 ring-green-100">
                            <CalcIcon className="w-8 h-8" />
                        </div>
                        <div>
                            <h1 className="text-3xl font-extrabold text-slate-900 tracking-tight">Academic Planner</h1>
                            <p className="text-slate-500 font-medium">IIT Madras BS Degree Curriculum</p>
                        </div>
                    </div>

                    <div className="flex flex-wrap gap-3 mt-6">
                        <div className="space-y-1.5 flex-1 min-w-[200px]">
                            <Label className="text-xs font-bold uppercase tracking-wider text-slate-400 ml-1">Program Domain</Label>
                            <Select value={domain} onValueChange={(v: DegreeDomain) => setDomain(v)}>
                                <SelectTrigger className="h-12 border-slate-200 bg-white/50 backdrop-blur-sm focus:ring-green-500 rounded-xl">
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="Data Science">BS in Data Science</SelectItem>
                                    <SelectItem value="Electronic Systems">BS in Electronic Systems</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-1.5 flex-1 min-w-[200px]">
                            <Label className="text-xs font-bold uppercase tracking-wider text-slate-400 ml-1">Curriculum Level</Label>
                            <Select value={level} onValueChange={(v: Level) => setLevel(v)}>
                                <SelectTrigger className="h-12 border-slate-200 bg-white/50 backdrop-blur-sm focus:ring-green-500 rounded-xl">
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="Foundation text-green-700">1. Foundation</SelectItem>
                                    <SelectItem value="Diploma">2. Diploma</SelectItem>
                                    <SelectItem value="BSc Degree">3. BSc Degree</SelectItem>
                                    <SelectItem value="BS Degree">4. BS Degree</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                    </div>
                </div>

                {/* Dashboard Card */}
                <Card className="border-green-100 shadow-2xl shadow-green-900/10 bg-gradient-to-br from-green-600 to-emerald-700 text-white overflow-hidden relative border-none rounded-3xl">
                    <div className="absolute top-0 right-0 w-32 h-32 bg-white/10 rounded-full -mr-16 -mt-16 blur-2xl"></div>
                    <CardHeader className="pb-2">
                        <CardTitle className="text-green-100 text-xs font-bold uppercase tracking-widest flex items-center gap-2">
                            Overall Standing
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="flex items-baseline gap-2">
                            <span className="text-6xl font-black tracking-tighter">{stats.cgpa}</span>
                            <span className="text-green-100/70 font-semibold">/ 10.0</span>
                        </div>
                        <div className="mt-4 flex flex-col gap-2">
                            <div className="flex justify-between items-center bg-white/10 p-2 px-3 rounded-xl border border-white/10">
                                <span className="text-xs font-medium text-green-50">Passed Credits</span>
                                <span className="font-bold">{stats.totalCredits}</span>
                            </div>
                            <div className="flex justify-between items-center bg-white/10 p-2 px-3 rounded-xl border border-white/10">
                                <span className="text-xs font-medium text-green-50">Courses Graded</span>
                                <span className="font-bold">{stats.courseCount}</span>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Course Selection Table */}
            <Card className="border-slate-200/60 shadow-xl shadow-slate-200/50 rounded-3xl overflow-hidden border-none bg-white/80 backdrop-blur-xl">
                <CardHeader className="bg-slate-50/50 border-b border-slate-100 flex flex-row items-center justify-between p-6">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-xl bg-slate-100 flex items-center justify-center text-slate-600">
                            {level === 'Foundation' && <ScrollText className="w-5 h-5" />}
                            {level === 'Diploma' && <Award className="w-5 h-5" />}
                            {level === 'BSc Degree' && <GradingIcon level="BSc" />}
                            {level === 'BS Degree' && <GraduationCap className="w-5 h-5" />}
                        </div>
                        <div>
                            <CardTitle className="text-xl font-bold text-slate-800">{level} Coursework</CardTitle>
                            <CardDescription>Select grades for courses taken at this level.</CardDescription>
                        </div>
                    </div>
                    <Button variant="ghost" size="sm" onClick={resetLevelGrades} className="text-slate-400 hover:text-red-500 hover:bg-red-50 transition-colors">
                        <RotateCcw className="w-4 h-4 mr-2" /> Reset Level
                    </Button>
                </CardHeader>
                <CardContent className="p-0">
                    <div className="overflow-x-auto">
                        <Table>
                            <TableHeader className="bg-slate-50/30">
                                <TableRow className="border-slate-100 hover:bg-transparent">
                                    <TableHead className="w-[450px] pl-8">Official Course Name</TableHead>
                                    <TableHead className="text-center">Credits</TableHead>
                                    <TableHead className="text-right pr-8">Grade Selection</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                <AnimatePresence mode="popLayout">
                                    {currentCourses.map((course) => (
                                        <motion.tr
                                            key={course.id}
                                            initial={{ opacity: 0, y: 10 }}
                                            animate={{ opacity: 1, y: 0 }}
                                            exit={{ opacity: 0, scale: 0.95 }}
                                            className="group border-slate-50 hover:bg-slate-50/40 transition-all"
                                        >
                                            <TableCell className="pl-8 py-5">
                                                <div className="flex items-center gap-3">
                                                    {course.grade ? (
                                                        <CheckCircle2 className="w-5 h-5 text-green-500" />
                                                    ) : (
                                                        <Circle className="w-5 h-5 text-slate-200" />
                                                    )}
                                                    <span className={`font-semibold text-slate-700 transition-colors ${course.grade ? 'text-slate-900' : 'text-slate-400'}`}>
                                                        {course.name}
                                                    </span>
                                                </div>
                                            </TableCell>
                                            <TableCell className="text-center">
                                                <span className="px-2.5 py-1 bg-slate-100 text-slate-600 rounded-lg text-xs font-bold ring-1 ring-slate-200">
                                                    {course.credits} CR
                                                </span>
                                            </TableCell>
                                            <TableCell className="text-right pr-8">
                                                <Select
                                                    value={course.grade ?? "not_taken"}
                                                    onValueChange={(v) => updateGrade(course.name, v === "not_taken" ? null : v as Grade)}
                                                >
                                                    <SelectTrigger className={`w-36 ml-auto h-10 border-none shadow-none focus:ring-0 rounded-xl transition-all ${course.grade ? 'bg-green-50 text-green-700 font-bold' : 'bg-slate-50 text-slate-400'
                                                        }`}>
                                                        <SelectValue />
                                                    </SelectTrigger>
                                                    <SelectContent className="rounded-xl border-slate-200 shadow-xl">
                                                        <SelectItem value="not_taken" className="text-slate-400 italic">Not Taken</SelectItem>
                                                        <SelectItem value="S" className="text-green-600 font-bold">S — 10</SelectItem>
                                                        <SelectItem value="A" className="text-green-600 font-bold">A — 09</SelectItem>
                                                        <SelectItem value="B" className="text-blue-600 font-bold">B — 08</SelectItem>
                                                        <SelectItem value="C" className="text-blue-600 font-bold">C — 07</SelectItem>
                                                        <SelectItem value="D" className="text-amber-600 font-bold">D — 06</SelectItem>
                                                        <SelectItem value="E" className="text-amber-600 font-bold">E — 04</SelectItem>
                                                        <SelectItem value="U" className="text-red-500">U — Fail</SelectItem>
                                                        <SelectItem value="I" className="text-slate-400">I — Incomplete</SelectItem>
                                                        <SelectItem value="W" className="text-slate-400">W — Withdrawn</SelectItem>
                                                    </SelectContent>
                                                </Select>
                                            </TableCell>
                                        </motion.tr>
                                    ))}
                                </AnimatePresence>
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>

            {/* Footer Information */}
            <div className="flex flex-col md:flex-row gap-6 p-6 bg-slate-50/50 rounded-[2.5rem] border border-slate-100">
                <div className="flex items-start gap-4 flex-1">
                    <div className="p-3 bg-white rounded-2xl shadow-sm border border-slate-100">
                        <Info className="w-5 h-5 text-blue-500" />
                    </div>
                    <div>
                        <h4 className="font-bold text-slate-800 text-sm">Grading Policy</h4>
                        <p className="text-xs text-slate-500 mt-1 leading-relaxed">
                            CGPA is computed as a weighted average. Courses marked as &quot;Not Taken&quot; or failing grades (U, I, W) are strictly excluded from the computation as per IIT Madras BS regulations.
                        </p>
                    </div>
                </div>
                <div className="flex items-center gap-2 text-[10px] text-slate-400 font-medium whitespace-nowrap md:border-l md:pl-6 md:border-slate-200">
                    <CheckCircle2 className="w-3 h-3" />
                    100% Client-Side Logic. Privacy First.
                </div>
            </div>
        </div>
    );
}

function GradingIcon({ level }: { level: string }) {
    return (
        <div className="font-black text-xs tracking-tighter text-slate-400">{level}</div>
    );
}

function CardDescription({ children }: { children: React.ReactNode }) {
    return <p className="text-xs text-slate-500 font-medium">{children}</p>;
}
